@import "../../main.scss";

.wrapper {
    text-align: end;
}


.item {
    height: 50px;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    user-select: none;
    color: $fifthColor;
    transition: color .2s;
    cursor: pointer;

    &:hover {
        color: $fourthColor;

        & .itemBorder {
            background-color: $fourthColor;
        }
    }

    &.active {
        color: $fourthColor;
    }
}

.itemText {
    font-size: 20px;
    font-weight: 800;
    padding-right: 15px;
}


.itemBorder {
    height: 100%;
    width: 4px;
    background-color: $fifthColor;
    border-radius: 2px;
    transition: background-color .2s;
}