@import "../../main.scss";


.wrapper {
    position: relative;
    width: 100%;
}

.section {
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.treeBackground {
    position: absolute;
    top: -30vh;
    left: 50vw;
    z-index: -10;

}

.container {
    padding-top: $headerHeight;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.description {
    margin-top: 15vh;
    color: $thirdColor;
    margin-left: $desctop1Indentation;
    margin-right: $desctop1Indentation*2;
    
    @include desktop1440 {
        margin-top: 10vh;
        margin-left: $desctop2Indentation;
        margin-right: $desctop2Indentation*2;
    }
}

.title {
    font-size: 48px;
    margin-bottom: 30px;
}

.descriptionText {
    max-width: 60vw;
    font-size: 20px;
    font-weight: 400;
}

.descriptionInfo {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 10vh;
    user-select: none;
}

.mainButton {
    display: flex;
    align-items: center;
    height: 70px;
    padding-right: $desctop1Indentation;
    font-size: $titleFontSize;
    font-weight: 700;
    padding-left: 20px;
    background-color: $firstColor;
    border-radius: 20px 0px 0px 20px;
    color: $seventhColor;
    cursor: pointer;
    transition: background-color .5s;

    &:hover {
        background-color: $secondColor;
    }

    @include desktop1440 {
        padding-right: $desctop2Indentation;
    }
}

.menu {
    position: fixed;
    right: 0;
    top: 30vh;
    padding-right: $desctop1Indentation;

    @include desktop1440 {
        padding-right: $desctop2Indentation;
    }
}