@import "../../main.scss";

.wrapper {
    display: flex;
    justify-content: left;
    margin-bottom: 5vh;

    &:last-child {
        margin-bottom: 0;
    }
}

.container {
    background-color: $sixthColor;
    padding-left: $desctop1Indentation;
    display: flex;
    justify-content: left;
    align-items: center;
    border: 1px solid #E4E4E2;
    border-radius: 0px 20px 20px 0px;
    
    @include desktop1440 {
        padding-left: $desctop2Indentation;
    }
}

.icon {
    height: 100px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
}

.description {
    display: flex;
    flex-direction: column;
    color: $fourthColor;
    margin-right: 30px;
    margin-bottom: 5px;
}

.count {
    font-size: $titleFontSize;
    font-weight: 600;
    height: $titleFontSize;
    margin-bottom: 5px;
    font-family: Arial;
}

.descriptionText {
    font-size: $commonFontSize;
    font-weight: 700;
}